<script>
/* eslint-disable */ 
</script>
<template>
    <div style="overflow: hidden;" @click="clickNow">
        <canvas ref="canvas"></canvas>
    </div>
</template>

<script>
import OnEvent from '../common/OnEvent'
import defaultImg from '../../assets/title.jpg'

export default {
    extends: OnEvent,
    props: {
        propValue: {
            type: Object,
            required: true,
            default: () => {},
            // eslint-disable-next-line
            url: 'default'
        },
        element: {
            type: Object,
            default: () => {},
        },
    },
    data() {
        return {
            width: 0,
            height: 0,
            img: null,
            canvas: null,
            ctx: null,
            isFirst: true,
            img_url:null,
        }
    },
    watch: {
        'element.style.width': function () {
            this.drawImage()
        },
        'element.style.height': function () {
            this.drawImage()
        },
        'propValue.url': async function () {
            await this.drawImage()
        },
        'propValue.flip.vertical': function () {
            this.mirrorFlip()
        },
        'propValue.flip.horizontal': function () {
            this.mirrorFlip()
        },
    },
    async mounted() {
        this.canvas = this.$refs.canvas
        this.ctx = this.canvas.getContext('2d')
    
        // setTimeout(async ()=>{
        // this.xdata = this.element.xdata
        // console.log('picturn:xdata:',this.xdata)
        // this.poplang = new PopRuntime(this)//this.element.poplang
        // let pic_run_rets = await this.poplang.runScript(null,this.element.code)
        // console.log('code:' + this.element.code , 'url:',this.propValue,'pic_run_rets:',pic_run_rets,'img_url:',this.img_url)
        // this.drawImage()
        // },1000)
        this.poplang = new PopRuntime(this)
        this.poplang.binderAddOpcode('init', async () => {
            this.xdata = this.element.xdata
            console.log('pic-this.xdata:', this.xdata)
            let pic_run_rets = await this.poplang.runScript(null,this.element.code)
            console.log('code:' + this.element.code , 'url:',this.propValue,'pic_run_rets:',pic_run_rets,'img_url:',this.img_url)
            this.drawImage()
        })
        this.element.poplang = this.poplang

        this.drawImage()
        if (this.poplang) {
            // this.poplang.runScript({}, this.element.code)
            // let results = await this.poplang.runScript(this.element, this.element.code, true)
            // console.log('script-results:' + JSON.stringify(this.poplang.deepCopy(results)))
            // let This = this
            // setTimeout(async () => {
            //     let results = await This.poplang.runScript({}, This.element.code)
            //     console.log('script-results:' + JSON.stringify(results))
            // }, 100)
        }

        // console.log('picture-onmounted!' + this.propValue)
        // this.propValue.url = ''
        // this.ctx.clearRect(0, 0, 1000, 1000)
        // this.drawImage()
    },
    methods: {
        async drawImage() {
            let This = this
            const { width, height } = this.element.style
            this.canvas.width = width
            this.canvas.height = height
            //2023-9-26新增（支持base64图片）
            let imgUrl = this.img_url ? this.img_url : this.propValue.url
            let isBaseUrl = imgUrl && imgUrl.startsWith('data:image/')
            if(imgUrl == 'default' || isBaseUrl)
            {
                this.img = document.createElement('img')
                this.img.onload = () => {
                    this.ctx.drawImage(This.img, 0, 0, width, height)
                    This.mirrorFlip()
                }
                this.img.src = isBaseUrl ? imgUrl : defaultImg
                return 
            }
            if (true || this.isFirst) {
                // this.isFirst = false
                this.img = document.createElement('img')
                this.img.setAttribute('crossOrigin', 'Anonymous')
                this.img.onload = () => {
                    this.ctx.drawImage(This.img, 0, 0, width, height)
                    This.mirrorFlip()
                }
                // this.img.src = this.propValue.url
                // this.img.onload = () => {
                //     this.ctx.drawImage(this.img, 0, 0, width, height)
                //     this.mirrorFlip()
                // }

                let img_id = imgUrl
                // let params = {user_id:localStorage.getItem('user_id'),s_id:localStorage.getItem('s_id'),
                //         filename:img_id,img_kind:'open'}//,img_p:'min200'}
                let params = img_id && img_id.startsWith('dtns://') ? {}:{filename:img_id,img_kind:'open'}
                let reqUrl = img_id && img_id.startsWith('dtns://')  ? img_id :''
                let item = await imageDb.getDataByKey(img_id)//localStorage.getItem('chatlogo-'+chatInfo.chatlogo)
                if(item && item.data){ 
                    this.img.src = item.data;
                }
                else{
                    let This = this
                    queryImg(reqUrl,params).then(async (data)=>{
                        let tmpData = 'data:image/png;base64,'+data.data
                        This.img.src = tmpData
                        imageDb.addData({img_id,data:tmpData })
                    })
                }
            } else {
                this.mirrorFlip()
            }
        },

        
        async clickNow(e) {
            let ret = await this.poplang.op(null, 'onclick')
            console.log('pic-onclick-ret:', ret)
        },

        mirrorFlip() {
            const { vertical, horizontal } = this.propValue.flip
            const { width, height } = this.element.style
            const hvalue = horizontal ? -1 : 1
            const vValue = vertical ? -1 : 1

            // 清除图片
            this.ctx.clearRect(0, 0, width, height)
            // 平移图片
            this.ctx.translate(width / 2 - width * hvalue / 2, height / 2 - height * vValue / 2)
            // 对称镜像
            this.ctx.scale(hvalue, vValue)
            this.ctx.drawImage(this.img, 0, 0, width, height)
            // 还原坐标点
            this.ctx.setTransform(1, 0, 0, 1, 0, 0)
        },
    },
}
</script>
